

  body{
    display: flex;
    justify-content: center;
   background-color: blanchedalmond;
}

@media screen and (max-width: 480px) {
    body {
        background-color: blanchedalmond;
    }

    .navbar{
      display: flex;
      justify-content: center;
    }

    .reactiveTails{
        display: flex;
        justify-content: center;
        color: rgb(12, 11, 11);
        margin-left: 20px;
    }
  }






.reactiveTails{
    display: flex;
    justify-content: center;
    color: rgb(12, 11, 11);
}

.navCenter,a,li{
    text-decoration: none;
    list-style-type: none;
    font-size: 2vw;
    line-height: 1.8;
    text-shadow: 2px 2px 5px red;
}

.sectionSearch { 
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    text-transform: uppercase;
    text-shadow: 2px 2px 5px red;
    -webkit-animation: mySearch 3s infinite;
            animation: mySearch 3s infinite;
}

@-webkit-keyframes mySearch {
    50% {color: rgb(137, 221, 28);}
}

@keyframes mySearch {
    50% {color: rgb(137, 221, 28);}
}

input{
    margin: 10px;
    height: 30px;
    width: 200px;
    border-radius: 5%;
    color: rgb(200, 89, 34);
    -webkit-animation: myPlaceholder 3s infinite;
            animation: myPlaceholder 3s infinite;
    text-indent: 15px;
    background-color: yellow;
}

@-webkit-keyframes myPlaceholder {
    0% {border-top-left-radius: 60%;}
    25% {border-top-right-radius: 50%;}
    75% {border-bottom-right-radius: 50%;}
    100% {border-bottom-left-radius: 60%;}
  }

@keyframes myPlaceholder {
    0% {border-top-left-radius: 60%;}
    25% {border-top-right-radius: 50%;}
    75% {border-bottom-right-radius: 50%;}
    100% {border-bottom-left-radius: 60%;}
  }


.cocktailTitle{
    display: flex;
    justify-content: center;
    margin-top: 25px;
    font-size: 10vw;
    font-style: italic;
    text-shadow: 2px 2px 5px red;
    -webkit-animation: myCocktails 5s infinite;
            animation: myCocktails 5s infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes myCocktails{
   0% {-webkit-transform: rotate(15deg);transform: rotate(15deg);}
    50% {-webkit-transform: rotate(-15deg);transform: rotate(-15deg);}
}

@keyframes myCocktails{
   0% {-webkit-transform: rotate(15deg);transform: rotate(15deg);}
    50% {-webkit-transform: rotate(-15deg);transform: rotate(-15deg);}
}

article {
    width: 500px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(21, 21, 21, 0.19);
    padding: 20px 30px 20px 0px;
    margin: 10px ;
    border-radius: 10%;
   background-color: black;
   
}

.name{
color: deeppink;
font-size: 40px;
}

.glass{
color: greenyellow;
font-size: 20px;
}

.info{
    color: rgb(255, 0, 0);
    font-size: 20px;
}

.details{
color: rgb(12, 29, 140);
-webkit-animation: myDetails 1s infinite;
        animation: myDetails 1s infinite;
}

@-webkit-keyframes myDetails {
    50% {color: rgb(205, 120, 23);}
  }

@keyframes myDetails {
    50% {color: rgb(205, 120, 23);}
  }

.cocktailsCenter{
    display: flex;
    flex-wrap: wrap;
} 

img{
    width: 200px;
    border-radius: 10%;
}

.cocktailFooter{
    
    width: 200px;
}

.btn {
    -webkit-animation: myBtn 3s infinite;
            animation: myBtn 3s infinite;
}

@-webkit-keyframes myBtn {
    50% {color: rgb(137, 221, 28);}
}

@keyframes myBtn {
    50% {color: rgb(137, 221, 28);}
}

.section-title{
    text-shadow: 2px 2px 5px red;
    font-size: 35px;
}
 
.drink-data{
    text-transform: capitalize;
    font-size: 20px;
    text-shadow: 2px 2px 5px red;
}

.about{
font-size: 35px;
font-family: fantasy;
line-height: 2em;
color: white;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}


